<template>

  <div id="app">
    <div class="l-block">
      <NotesList></NotesList>
    </div>
    <div class="l-block">
      <form class="form" @submit="addNote">
        <div class="form__item">
          <input v-model="newNote.title" type="text" placeholder="title">
        </div>
        <div class="form__item">
          <textarea v-model="newNote.text" placeholder="text"></textarea>
        </div>
        <button type="submit">add</button>
        <div class="form__errors">
          <ul>
            <li v-for="error in newNote.errors" v-bind:key="error">{{error}}</li>
          </ul>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
// import HelloWorld from './components/HelloWorld.vue'
import NotesList from './components/NotesList.vue'
import { db, Timestamp } from './db';

export default {
  name: 'app',
  data: () => ({ 
    notes: [],
    newNote: {
      title: '',
      text: '',
      createdAt: '',
      errors: []
    }
  }),
  firestore: {
    notes: db.collection('todos'),
  },
  components: {
    NotesList
    // HelloWorld
  },
  created: function () {
    
  },
  methods: {
    addNote: function (e) {
      e.preventDefault();

       this.newNote.errors= [];

      if ( this.newNote.title.length == 0) {
        this.newNote.errors.push(`field 'title' can't been empty`);
      }

      if ( this.newNote.title.length == 0) {
        this.newNote.errors.push(`field 'text' can't been empty`);
      }

      if (this.newNote.errors.length == 0) {
        // this.notes.push()
        db.collection('todos').add({
          title: this.newNote.title,
          text: this.newNote.text,
          createdAt: Timestamp.fromDate(new Date()),
        })
      }

      return false;
    }
  }
}
</script>

<style lang="scss">
body{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background: #292929;
  font-size: 14px;
   font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #7f787a;
}

* {
  box-sizing: border-box;
}

#app {
  padding: 15px;
}

.l-block{
  background: #1d1d1d;
  border: 1px solid #484848;
  border-radius: 5px;
  padding: 20px;
  text-align: left;
  margin-bottom: 15px;

  &:last-child{
    margin-bottom: 0;
  }

}


</style>
